// __TESTS__/screens/MyGoalsScreen.test.tsx
// @ts-nocheck
import React from 'react';
import { render, screen, fireEvent, waitFor } from '@testing-library/react-native';
import MyGoalsScreen from '../../src/screens/MyGoalsScreen';
import goalService from '../../src/services/api/goalService';
import emotionService from '../../src/services/api/emotionService';

// 다른 모킹
jest.mock('@react-navigation/native', () => ({
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
  }),
}));

jest.mock('../../src/services/api/goalService', () => ({
  getGoals: jest.fn(),
  createGoal: jest.fn(),
  deleteGoal: jest.fn(),
}));

jest.mock('../../src/services/api/emotionService', () => ({
  getAllEmotions: jest.fn(),
}));

jest.mock('@react-native-community/datetimepicker', () => {
  return {
    __esModule: true,
    default: function MockDateTimePicker() {
      return null;
    },
    display: {
      default: 'default',
      spinner: 'spinner',
      calendar: 'calendar',
      clock: 'clock',
    },
  };
});

// 테스트 데이터
const mockGoals = [
  {
    goal_id: 1,
    target_emotion_id: 1,
    emotion_name: '행복',
    emotion_color: '#FFD700',
    start_date: '2025-03-01',
    end_date: '2025-04-30',
    progress: 60,
  },
  {
    goal_id: 2,
    target_emotion_id: 2,
    emotion_name: '편안함',
    emotion_color: '#32CD32',
    start_date: '2025-03-15',
    end_date: '2025-05-15',
    progress: 30,
  },
];

const mockEmotions = [
  { emotion_id: 1, name: '행복', icon: 'emoticon-happy-outline', color: '#FFD700' },
  { emotion_id: 2, name: '편안함', icon: 'sofa-outline', color: '#32CD32' },
];

describe('MyGoalsScreen', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    
    // 기본 모킹 설정
    goalService.getGoals.mockResolvedValue({
      data: { data: mockGoals },
    });
    
    emotionService.getAllEmotions.mockResolvedValue({
      data: { data: mockEmotions },
    });
  });

  it('renders loading state initially', () => {
    render(<MyGoalsScreen />);
    // ActivityIndicator가 표시되는지 확인
    expect(screen.getByTestId('loading-indicator')).toBeTruthy();
  });

  it('renders goals list correctly', async () => {
    const { getByText, findByText } = render(<MyGoalsScreen />);
    
    await waitFor(async () => {
      expect(await findByText('나의 감정 목표')).toBeTruthy();
      // FlatList가 렌더링 된 후에 아이템을 찾음
      expect(await findByText(/행복/)).toBeTruthy();
      expect(await findByText(/편안함/)).toBeTruthy();
      expect(await findByText(/60%/)).toBeTruthy();
      expect(await findByText(/30%/)).toBeTruthy();
    }, { timeout: 10000 });
  });

  it('shows empty state when no goals', async () => {
    goalService.getGoals.mockResolvedValue({
      data: { data: [] },
    });
    
    const { findByText } = render(<MyGoalsScreen />);
    
    await waitFor(async () => {
      expect(await findByText('아직 설정된 감정 목표가 없습니다.')).toBeTruthy();
      expect(await findByText('새 목표 추가하기')).toBeTruthy();
    }, { timeout: 10000 });
  });

  it('toggles create form visibility', async () => {
    const { findByText, queryByText } = render(<MyGoalsScreen />);
    
    // 로딩 상태가 끝날 때까지 기다림
    const addButton = await findByText('새 목표 추가');
    
    fireEvent.press(addButton);
    
    expect(await findByText('새 감정 목표 생성')).toBeTruthy();
    
    const cancelButton = await findByText('취소');
    fireEvent.press(cancelButton);
    
    await waitFor(() => {
      expect(queryByText('새 감정 목표 생성')).toBeNull();
    });
  });

  it('validates emotion selection on form submission', async () => {
    // 모의 구현
    goalService.createGoal.mockImplementation(() => {
      throw new Error('이 함수는 호출되면 안됩니다');
    });
    
    const { findByText } = render(<MyGoalsScreen />);
    
    // 새 목표 추가 버튼 찾고 클릭
    const addButton = await findByText('새 목표 추가');
    fireEvent.press(addButton);
    
    // 폼 표시 확인
    const formTitle = await findByText('새 감정 목표 생성');
    expect(formTitle).toBeTruthy();
    
    // 감정 선택 없이 바로 제출
    const submitButton = await findByText('목표 생성하기');
    fireEvent.press(submitButton);
    
    // Alert 대신 간접적으로 검증: createGoal이 호출되지 않는지 확인
    expect(goalService.createGoal).not.toHaveBeenCalled();
    
    // 폼이 계속 표시되는지 확인 (Alert로 인해 제출이 중단)
    await waitFor(() => {
      expect(formTitle).toBeTruthy();
    });
  });

  it('submits form successfully', async () => {
    // 모의 구현
    goalService.createGoal.mockResolvedValue({
      data: {
        data: {
          goal_id: 3,
          target_emotion_id: 1,
          emotion_name: '행복',
          emotion_color: '#FFD700',
          start_date: '2025-04-01',
          end_date: '2025-05-01',
          progress: 0,
        },
      },
    });
    
    const { findByText, getAllByText, queryByText } = render(<MyGoalsScreen />);
    
    // 새 목표 추가 버튼 찾기
    const addButton = await findByText('새 목표 추가');
    fireEvent.press(addButton);
    
    // 폼 표시 확인
    await findByText('새 감정 목표 생성');
    
    // 감정 선택 - getAllByText 대신 테스트 ID나 역할로 찾도록 수정
    const happyButtons = await waitFor(() => getAllByText(/행복/));
    fireEvent.press(happyButtons[0]);
    
    // 폼 제출
    const submitButton = await findByText('목표 생성하기');
    fireEvent.press(submitButton);
    
    // 목표 생성 함수가 호출되었는지 확인
    await waitFor(() => {
      expect(goalService.createGoal).toHaveBeenCalled();
    });
    
    // 폼이 닫혔는지 확인 (성공 후 resetForm 호출)
    await waitFor(() => {
      expect(queryByText('새 감정 목표 생성')).toBeNull();
    });
  });

  it('handles goal deletion', async () => {
    // 모의 구현
    goalService.deleteGoal.mockResolvedValue({
      data: { success: true }
    });
    
    const { findByText, findAllByTestId } = render(<MyGoalsScreen />);
    
    // 목록 로드 확인
    await findByText('나의 감정 목표');
    
    // 삭제 버튼을 테스트 ID로 찾도록 수정
    const deleteButtons = await findAllByTestId('delete-goal-button');
    expect(deleteButtons.length).toBeGreaterThan(0);
    
    fireEvent.press(deleteButtons[0]);
    
    // deleteGoal이 호출되지는 않았는지 확인 (Alert 확인 단계에서 멈춤)
    expect(goalService.deleteGoal).not.toHaveBeenCalled();
  });

  it('shows error state when loading fails', async () => {
    goalService.getGoals.mockRejectedValue(new Error('네트워크 오류'));
    
    const { findByText } = render(<MyGoalsScreen />);
    
    await waitFor(async () => {
      expect(await findByText('데이터를 불러오는 중 오류가 발생했습니다.')).toBeTruthy();
      expect(await findByText('다시 시도')).toBeTruthy();
    });
  });

  it('handles refresh', async () => {
    // 모의 구현
    goalService.getGoals
      .mockRejectedValueOnce(new Error('네트워크 오류'))
      .mockResolvedValueOnce({
        data: { 
          data: [
            {...mockGoals[0], progress: 70},
            {...mockGoals[1], progress: 40},
          ] 
        },
      });
    
    const { findByText } = render(<MyGoalsScreen />);
    
    // 오류 상태 확인
    const errorText = await findByText('데이터를 불러오는 중 오류가 발생했습니다.');
    expect(errorText).toBeTruthy();
    
    // 다시 시도 버튼 클릭
    const refreshButton = await findByText('다시 시도');
    fireEvent.press(refreshButton);
    
    // 새로고침 확인
    await waitFor(() => {
      expect(goalService.getGoals).toHaveBeenCalledTimes(2);
    });
    
    // 데이터가 로드된 후 제목이 보이는지 확인
    await findByText('나의 감정 목표');
  });
});